import CourseDetailsViewModel from '@/viewmodels/Course/CourseDetails';
import Course from '@/models/Course';

@Component
export default class extends Vue {
   openDialog = false;
   course: Course = {} as Course;

   // Hook
   created() {
      const courseDetailsViewModel = new CourseDetailsViewModel();

      console.log(this.$route.params.id);
   }

   scrollToTitle(elementID: string) {
      document.getElementById(elementID)?.scrollIntoView(true);
   }

   addActiveClass(titleBtnRef: any) {
      const titleBtnList: HTMLCollectionOf<Element> = document.getElementsByClassName('title-btn');

      // Remove 'side-bar-link' active class
      for (let i = 0; i < titleBtnList.length; i++) titleBtnList[i].classList.remove('active-title-btn');

      (titleBtnRef as HTMLElement).classList.add('active-title-btn');
   }
}
